@charset "UTF-8";

html,
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  touch-action: none;
  height: 100%;
  font-family: Arial, sans-serif;
}

#map {
  width: 100vw;
  height: 100vh;
  background: #f0f0f0;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
}

svg {
  user-select: none;
  display: block;
  touch-action: none;
  cursor: grab;
}

#inner {
  transform-origin: 0 0;
  transition: none;
}

/* Контейнер для кнопок */

#controls {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 10;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

#controls button {
  background: white;
  border: 1px solid #888;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  font-size: 24px;
  line-height: 1;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.2s;
}

#controls button:hover {
  background-color: #eee;
}

#controls button:active {
  background-color: #ccc;
}

.balloon {
  position: absolute;
  bottom: 0;
  left: 0;
  background: #fff;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  z-index: 10;
  width: 100%;
  max-width: 320px;
  padding: 25px;
}

.balloon h2,
.balloon p {
  margin: 0;
  padding: 0;
}

.balloon__close-button {
  position: absolute;
  top: -20px;
  right: -20px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}